.container {
  display: flex;
  flex-flow: column;
  width: 100%;
  height: 100%; }
  .container__index {
    background: #2C3339 url("../img/bg_index.jpg") 100% 100% no-repeat fixed;
    background-size: cover; }
    @media screen and (max-width: 768px) {
      .container__index {
        background: #2C3339 url("../img/bg_index-tab.jpg") 100% 100% no-repeat fixed;
        background-size: cover; } }
    @media screen and (max-width: 480px) {
      .container__index {
        background: #2C3339 url("../img/bg_index-mob.jpg") 100% 100% no-repeat fixed;
        background-size: cover; } }

.header-index {
  justify-content: flex-end; }
  @media screen and (max-width: 480px) {
    .header-index {
      justify-content: space-between; } }

.header__address {
  font-family: PlumbC;
  font-size: 16px;
  font-style: normal;
  color: #CED5D8; }
  @media screen and (max-width: 480px) {
    .header__address {
      display: none; } }

.header__phone {
  font-family: PlumbC;
  font-size: 24px;
  font-weight: 700;
  color: #CED5D8;
  text-decoration: none;
  padding-left: 2%;
  padding-right: 2%; }
  @media screen and (max-width: 480px) {
    .header__phone {
      color: #2C3339;
      font-size: 28px;
      margin-right: 5%;
      margin-top: 8%; } }

.header__logo-mob {
  display: none; }
  @media screen and (max-width: 480px) {
    .header__logo-mob {
      display: block;
      width: 8%;
      margin-left: 3%;
      margin-top: 5%; } }

.nav__icon {
  margin-top: 65px;
  margin-left: 8%;
  margin-bottom: 50px; }
  @media screen and (max-width: 480px) {
    .nav__icon {
      display: none; } }

.nav__list {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-left: 8%; }

.nav__links {
  color: #CED5D8;
  text-decoration: none;
  font-family: PlumbC;
  font-weight: bold;
  font-size: 18px;
  line-height: 48px; }

.nav__links-index {
  color: #2C3339; }

.nav__lang {
  list-style: none;
  padding: 0;
  margin: 0;
  margin-left: 8%;
  margin-top: auto;
  display: flex;
  width: 102px;
  justify-content: space-around; }

.lang__item {
  flex: 1;
  display: flex;
  justify-content: center; }

.lang__item-active {
  border: 2px solid #CED5D8; }

.lang__links {
  color: #CED5D8;
  text-decoration: none;
  font-family: PlumbC;
  font-weight: bold;
  font-size: 18px;
  line-height: 48px; }

* {
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box; }

html, body {
  height: 100%; }

body {
  font-family: Roboto, sans-serif;
  /*
      Roboto, 100, 100 italic, 300, 300 italic, 400, 400 italic, 500, 500 italic, 700, 700 italic, 900, 900 italic
  */
  line-height: 1.42;
  position: relative; }

.header {
  height: 60px;
  display: flex;
  align-items: center; }
  @media screen and (max-width: 480px) {
    .header {
      margin-bottom: 10%; } }

.main {
  display: flex;
  flex-direction: row;
  flex: 1;
  padding-bottom: 30px; }
  @media screen and (max-width: 480px) {
    .main {
      flex-flow: row wrap; } }

.nav {
  display: flex;
  flex: 3;
  flex-direction: column;
  justify-content: flex-start; }
  @media screen and (max-width: 480px) {
    .nav {
      flex: 100%;
      order: 2; } }

.index__content {
  display: flex;
  justify-content: center;
  align-items: center;
  flex: 9; }
  @media screen and (max-width: 480px) {
    .index__content {
      flex: 100%;
      order: 1; } }

.pagination {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center; }
  @media screen and (max-width: 480px) {
    .pagination {
      display: none; } }

.paginator {
  height: 180px;
  width: 22px; }

.index__logo {
  width: 72%; }
  @media screen and (max-width: 480px) {
    .index__logo {
      width: 84%; } }

.st0 {
  fill: #2C3339; }
