.nav__icon{
  margin-top: 65px;
  margin-left: 8%;
  margin-bottom: 50px;

  @include phones{
    display: none;
  }

}

.nav__list{
  list-style: none;
  padding: 0;
  margin: 0;
  margin-left: 8%;
}

.nav__links{
  color: #CED5D8;
  text-decoration: none;
  font-family : PlumbC;
  font-weight : bold;
  font-size : 18px;
  line-height : 48px;
}

.nav__links-index{
  color: #2C3339;
}

.nav__lang{
  list-style: none;
  padding: 0;
  margin: 0;
  margin-left: 8%;
  margin-top: auto;
  display: flex;
  width: 102px;
  justify-content: space-around;
}

.lang__item{
  flex: 1;
  display: flex;
  justify-content: center;
}

.lang__item-active{
  border: 2px solid #CED5D8;
}

.lang__links{
  color: #CED5D8;
  text-decoration: none;
  font-family : PlumbC;
  font-weight : bold;
  font-size : 18px;
  line-height : 48px;
}